#dcareer_main{
    padding: 0 2rem;
}

#dcareer_header{
    display: flex;
    justify-content: space-between;
    align-items: center;
    border: 2px solid var(--tranparent-20);
    border-radius: 1rem;
    padding: 1rem;
    color: var(--secondary);
    overflow: hidden;
    animation: slideAndFadeInFromUp 0.5s ease forwards;
    position: sticky;
    top: 1rem;
    background: var(--tranparent-10);
    backdrop-filter: blur(40px);
    z-index: 10;
}

#dcareer_header::before{
    content: '';
    position: absolute;
    top: var(--y);
    left: var(--x);
    transform: translate(-50%,-50%);
    background: radial-gradient(#ffb73c35,transparent,transparent);
    width:16rem;
    height: 16rem;
    transition: .3s,top 0s, left 0s;
    opacity: 0;
    z-index: -1;
}

#dcareer_header:hover::before{
    opacity: 1;
}

#dheader::after{
    content: '';
    inset: 2px;
}



#dcareer_header > div:nth-child(1){
    display: flex;
    align-items: center;
    gap: .5rem;
}

#dcareer_header > div:nth-child(1) > p:nth-child(2){
    opacity: .6;
}


#dcareer_header > div:nth-child(2) > button > img{
    width: 2rem;
    height: 2rem;
}


#dcareer_body{
    padding: 2rem 0;
    margin-top: 2rem;
    color: var(--secondary);
}

#dcareer_body > p {
    text-align: center;
    letter-spacing: .15rem;
    font-size: large;
    opacity: .6;
}


.grid_container{
    display: grid;
    grid-template-columns: repeat(3,1fr);
    gap: 2rem;
    padding: 4rem 6rem;
}

.grid_container > div {
    width: 100%;
    border: 2px solid var(--tranparent-20);
    border-radius: 1rem;
    padding: 2rem;
    display: flex;
    flex-direction: column;
    transition: .3s;
}

.grid_container > div:hover {
    transform: scale(1.05);
    background: var(--tranparent-10);
}

.dcareer_element > p:nth-child(1){
    text-align: center;
}

.dcareer_element > p:nth-child(2){
    text-align: center;
    opacity: .6;
    font-size: small;
    margin-top: 1rem;
    letter-spacing: .08rem;
    line-height: 1.2rem;
    flex: 1;
}

.dcareer_element > p:nth-child(3){
    font-size: smaller;
    opacity: .5;
    text-align: center;
    margin-top: 2rem;
}

.dcareer_element > p:nth-child(4){
    font-size: smaller;
    opacity: .5;
    text-align: center;
    margin-top: .5rem;
}

.dcareer_element_btn{
    align-self: center;
    padding: .5rem 2rem;
    margin-top: 2rem;
    border: 2px solid var(--secondary);
    border-radius: 2rem 1rem;
}

.dcareer_element_btn:hover{
    background: var(--tranparent-10);
}

.career_dialog{
    font-size: .9rem;
    user-select: text;
    color: var(--secondary);
}