#dsection1_heading{
    align-self: center;
    font-size: x-large;
    font-weight: 300;
    margin-top: 12rem;
    display: flex;
    justify-content: center;
}

#dsection1{
    grid-column-gap: 1rem;
    grid-row-gap: 1rem;
    flex-wrap: wrap;
    display: flex;
    justify-content: space-between;
    margin: 2rem 8rem;
    /* margin-top: 12rem; */
}

#dsection1>div{
    flex: auto;
    border: 1px solid var(--tranparent-20);
    backdrop-filter: blur(4px);
    border-radius: 1rem;
    padding: 2rem;
    transition: 1s;
    display: flex;
    flex-direction: column;
    justify-content: center;
    animation: slideAndFadeIn 0.5s ease forwards;
    
}

#dsection1>div:nth-child(1){
    flex: .5;
    background:linear-gradient(to right, #2e7bff10 , #b876ff05 , #ff72fd05 , #ff7cac05 , #ff7c7c10 );
}

#dsection1>div:nth-child(2){
    flex: .5;
    background:linear-gradient(to right, #2e7bff10 , #b876ff05 , #ff72fd05 , #ff7cac05 , #ff7c7c10 );
}
#dsection1>div:nth-child(3){
    transition: .3s;
    flex: .5;
    background: linear-gradient(to right, #2e7bff40 , #b876ff20 , #ff72fd20 , #ff7cac20 , #ff7c7c40 );

    &:hover{
