#ddialog {
    width: 100%;
    height: 100vh;
    position: fixed;
    left: 0;
    top: 0;
    background-color: var(--tranparent-50);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 50;
    transition: opacity 0.3s ease-in-out;
}

.ddialog_element {
    background-color: var(--primary);
    color: var(--secondary);
    min-width: 40%;
    width: auto;
    height: fit-content;
    padding: 2rem;
    border-radius: 1rem;
    box-shadow: 4px 4px 24px 4px var(--tranparent-20);

    /* Animation */
    animation: slideAndFadeIn 0.5s ease forwards;
    opacity: 0;
    transform: translateY(-50px);
}

@keyframes slideAndFadeIn {
    0% {
        opacity: 0;
        transform: translateY(50px);
    }
    100% {
        opacity: 1;
        transform: translateY(0);
    }
}


@keyframes slideAndFadeInFromUp {
    0% {
        opacity: 0;
        transform: translateY(-50px);
    }
    100% {
        opacity: 1;
        transform: translateY(0);
    }
}



.ddialog_element > p{
    font-size: large;
    text-align: center;
    padding: 1rem;
}

.ddialog_element > form {
    display: flex;
    flex-direction: column;
    gap: 1rem;
}

.ddialog_element > div > button{
    font-size: medium;
    width: fit-content;
    align-self: center;
    padding: 1rem;
}

.ddialog_close{
    font-size: 2rem;
    cursor: pointer;
    opacity: .5;

    &:hover{
        opacity: 1;
    }
}

