footer {
    width: 100%;
    padding: 4rem 8rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 10rem;
    border-top: 2px solid var(--tranparent-20);
    backdrop-filter: blur(40px);
    user-select: text;
}

.footer_element {
    flex: 1;
}

.footer_element:nth-child(1) {
    display: flex;
    align-items: center;
    font-size: .9rem;
    gap: .5rem;
}

.footer_element:nth-child(1) > div{
    display: flex;
    flex-direction: column;
    gap: .2rem;
}

.footer_element:nth-child(1) > div img{
    width: 2rem;
    filter: invert(var(--invert-value));
}

.footer_element:nth-child(2) {
    display: flex;
    justify-content: center;
    cursor: pointer;
}

.footer_element:nth-child(3) {
    text-align: end;
}

footer > div:nth-child(2) > div{
    display: flex;
    align-items: center;
    gap: .5rem;
}

footer > div:nth-child(3) > p:nth-child(1){
    color: var(--tranparent-cc);
    font-size: small;
    letter-spacing: .08rem;
}

