@media only screen and (max-width: 800px) {
    
    .btn_alpha{
        font-size: .8rem;
    }
    
    #dheader {
        margin: 0 1rem;
    }


    #dheader>div:nth-child(1) {
        gap: .2rem;
        font-size: medium;
        padding: 1rem;
    }

    #dheader>div>div:nth-child(1)>div:nth-child(2) {
        padding: 1rem;
    }

    #dheader>div>div:nth-child(1)>div:nth-child(1) {
        gap: .2rem;
    }


    #bg_glow1{
        width: 30rem;
        height: 20rem;
        left: -30rem;
        top: 5rem;
    }
    


    /****/

    #dhero>div:nth-child(2) img {
        height: 12rem;
        position: absolute;
        right: 0;
        z-index: -1;
    }

    #dlogo {
        width: 1.6rem;
        height: 1.6rem;
    }

    #dhero {
        margin-top: 4rem;
        padding: 2rem;
        font-size: 1.6rem;
    }

    #dhero_arrow {
        width: 8rem;
        transform: rotate(50deg);
        left: 50vw;
        top: 19rem;
    }

    #dhero>div>div {
        gap: 0rem;
    }

    #dhero>div>div>button {
        padding: .5rem 1rem;
        font-size: .9rem;
    }




    /***********/

    #dsection1{
        margin: 2rem 2rem;
        display: flex;
        flex-direction: column;
    }


    #dsection1>div{
        width: 100%;
    }



    /*****/

    #dfaq{
        padding: 2rem ;
    }
    
    .dfaq_element{
        margin-bottom: 1rem;
    }
    
    .dfaq_element > div:nth-child(1){
        padding: 1rem;
    }
    
    
    .dfaq_element > div:nth-child(2){
        padding: 1rem;
    }

    footer {
        padding: 2rem;
        font-size: .8rem;
    }

    .footer_element:nth-child(1) > div img{
        width: 1.2rem;
    }
    .footer_element:nth-child(1) > div{
        font-size: .6rem;
    }
}